* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}


body {
  font-size: 10px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba( 0, 0, 0, .4);
  color: #fff;
}

.app:before {
  content: ' ';
  background: url(./assets/sunset.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 4rem;
}

.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem; 
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, .8);
  background: none;
  color: #fff;
}

::placeholder {
  color: rgba(255, 255, 255, .8);
}

.container {
  max-width: 700px;
  height: 550px;
  margin: auto;
  padding:0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width:100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right:-90% ;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 80%;
  margin: 8rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, .2);
}
